<template>
    <div>
      <div class="left pro-tree">
        <el-input placeholder="请输入内容" v-model="inputVal" size="small">
          <el-button slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <TreeOrg :treeData="treeData" v-on:childByType="childByType"></TreeOrg>
      </div>
      <div class="left chart-wrap">
        <div class="margin-left" id="mapWrap" v-if="showmapWrap">
          <div class="loc-info">
            <div class="loc-title">农大南路燃气站</div>
            <div class="loc-footer"></div>
          </div>
          <div class="loc-point"><span></span></div>
        </div>
        <div class="margin-left pr" v-if="showcchpWrap">
          <vCCHP></vCCHP>
        </div>
        <div class="margin-left pr" v-if="showflowWrap">
          <vFlow></vFlow>
        </div>
      </div>
    </div>
</template>

<script>
  import echarts from "echarts";
  import vCCHP from "./cchp.vue";
  import vFlow from "./flow.vue";
  import TreeOrg from "../../../common/TreeOrg.vue";
    export default {
        data () {
            return {
              inputVal:'',
              showmapWrap:true,
              showcchpWrap:false,
              showflowWrap:false,
              treeData: [
                {
                  id:1,
                  label:"按站点查看",
                  type:"site",
                  children: [
                    {
                      id:11,
                      label: "唐家湾直流微网",
                      type:"site",
                      children: [
                        {
                          id:21,
                          label: "系统图",
                          type:"site",
                        },
                        {
                          id:22,
                          label: "储能子系统",
                          type:"site",
                        },
                        {
                          id:23,
                          label: "光伏",
                          type:"site",
                        }
                      ]
                    },
                    {
                      id:12,
                      label: "横琴多能网",
                      type:"site",
                    }
                  ]
                },
                {
                  id:2,
                  label:"按类型查看",
                  type:"type",
                  children: [
                    {
                      id:11,
                      label: "斗门区 (12)",
                      type:"type",
                      children: [
                        {
                          id:21,
                          label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)",
                          type:"type",
                        },
                        {
                          id:22,
                          label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)",
                          type:"type",
                        },
                        {
                          id:23,
                          label: "珠海新青科技工业园太阳能光伏发电项目(伟创力厂区)",
                          type:"type",
                        }
                      ]
                    },
                    {
                      id:12,
                      label: "金湾区 (6)",
                      type:"type",
                    }
                  ]
                }
              ],
            }
        },
      mounted(){

      },
      methods: {
        childByType:function(parm){
          let that = this;
          if(parm.type == 'site'){
            that.showmapWrap = false;
            that.showcchpWrap = true;
            that.showflowWrap = false;
          }else if(parm.type == 'type'){
            that.showmapWrap = false;
            that.showcchpWrap = false;
            that.showflowWrap = true;
          }
        },
      },
      updated(){
        let that = this;
      },
      components: {
        TreeOrg,vCCHP,vFlow
      },
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#mapWrap,#flowWrap,#cchpWrap{
  height: 812px;
  background: #ccc;
  position: relative;
}
  #powerLoadLine,#heatLoadLine{
    height: 150px;
  }
</style>



