<template>
  <div>
        <div id="cchpWrap"></div>
        <div class="right-quota">
          <el-col :span="24" class="bg-white hidden margin-bottom">
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">发电功率</p>
              <p><span class="t-val">132</span> <span class="t-text">kWh</span></p>
            </el-col>
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">发电频率</p>
              <p><span class="t-val">807</span> <span class="t-text">kWh</span></p>
            </el-col>
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">累计发电小时数</p>
              <p><span class="t-val">245</span> <span class="t-text">kWh</span></p>
            </el-col>
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">运行时长</p>
              <p><span class="t-val">309</span> <span class="t-text">kWh</span></p>
            </el-col>
          </el-col>
          <el-col :span="24" class="bg-white hidden margin-bottom">
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">总耗气量</p>
              <p><span class="t-val">132</span> <span class="t-text">kWh</span></p>
            </el-col>
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">总发电量</p>
              <p><span class="t-val">807</span> <span class="t-text">kWh</span></p>
            </el-col>
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">当日发电量</p>
              <p><span class="t-val">245</span> <span class="t-text">kWh</span></p>
            </el-col>
            <el-col :span="12" class="padding padding-left">
              <p class="t-text">日均发电量</p>
              <p><span class="t-val">309</span> <span class="t-text">kWh</span></p>
            </el-col>
          </el-col>
          <el-col :span="24" class="bg-white hidden margin-bottom">
            <el-col :span="12" class="align-center" style="margin-top: 30px">
              <el-progress type="circle" width=100 :percentage="98.88" ></el-progress>
              <p class="l-text">设备在线率</p>
            </el-col>
            <el-col :span="12" class="padding">
              <div class="margin-bottom">
                <p class="l-text align-left">供电量：186kW</p>
                <p class="l-text align-left">蓄电：186kW</p>
              </div>
              <div class="margin-bottom">
                <p class="l-text align-left">供冷量：186kW</p>
                <p class="l-text align-left">蓄冷：186kW</p>
              </div>
              <div class="margin-bottom">
                <p class="l-text align-left">供热量：186kW</p>
                <p class="l-text align-left">蓄热：186kW</p>
              </div>
            </el-col>
          </el-col>
          <el-col :span="24" class="bg-white hidden margin-bottom">
            <p class="e-title">发电实时负荷曲线</p>
            <div id="powerLoadLine"></div>
          </el-col>
        </div>
  </div>
</template>

<script>
  import echarts from "echarts";
  export default {
    data () {
      return {
      }
    },
    mounted(){
      let that = this;
      that.getpowerLoadLine();
    },
    methods: {
      getpowerLoadLine() {
        var that = this;
        var xData = [];
        var powerData = [];
        var gasData = [];
        var heatData = [];
        for(var i=0;i<24;i++){
          xData.push(i);
          powerData.push(Math.round(Math.random()*2*10));
          gasData.push(Math.round(Math.random()*2*10));
          heatData.push(Math.round(Math.random()*2*10));
        }
        // 基于准备好的dom，初始化echarts实例
        var myChart = echarts.init(document.getElementById("powerLoadLine"));
        // 指定图表的配置项和数据
        var option = {
//        dataset: {
//          source: that.data
//        },
          color: ["#0A6EDA","#F5A623"],
          title: {
            text: ""
          },
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
              crossStyle: {
                color: "#999"
              }
            }
          },
//            legend: {
//              data: ["电力负荷", "热力负荷","燃气负荷"],
//              right: 0
//            },
          grid: {
            x: "10%",
            y: "10%",
            x2: "3%",
            y2: "20%"
          },
          xAxis: {
            type: "category",
            //X轴数字
            axisLabel: {
              color: "#2B2B2B"
            },
            //X轴刻度线
            axisTick: {
              show: false
            },
            //纵向坐标网格线
            splitLine: {
              show: false,
              lineStyle: {
                color: "#e6e8ed"
              }
            },
            //X轴坐标线
            axisLine: {
              lineStyle: {
                color: "#e6e8ed"
              }
            },
            boundaryGap: false,
            data: xData
          },
          yAxis: {
            type: "value",
            //name:'%',
//              min:0,
//              max:100,
            //Y轴数字
            axisLabel: {
              color: "#2B2B2B"
            },
            //Y轴刻度线
            axisTick: {
              show: false
            },
            //横向坐标网格线
            splitLine: {
              lineStyle: {
                color: "#e6e8ed"
              }
            },
            //Y轴坐标线
            axisLine: {
              lineStyle: {
                color: "#e6e8ed"
              }
            }
          },
          series: [
            {
              name: "电力负荷",
              type: "line",
              symbol: 'none',
              stack: "总量",
              data:powerData,
            },
            {
              name: "热力负荷",
              type: "line",
              symbol: 'none',
              stack: "总量",
              data:gasData,
            }
          ]
        };
        // 使用刚指定的配置项和数据显示图表。
        myChart.setOption(option);
      },
    },
    updated(){

    },
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #mapWrap,#flowWrap,#cchpWrap{
    height: 812px;
    background: #ccc;
    position: relative;
  }
  #powerLoadLine,#heatLoadLine{
    height: 150px;
  }
</style>



